<template>
  <header>
    <v-app-bar :collapse='navCollapse' app elevate-on-scroll class="transparent pa-3 mybar" height="auto">
      <v-toolbar-title class="white--text font-weight-bold headline">AppLab</v-toolbar-title>
      <v-app-bar-nav-icon @click='drawer = !drawer' color="white" class="mr-10" v-if="navCollapse"></v-app-bar-nav-icon>
      <v-spacer></v-spacer>

      <v-list class="transparent d-flex pa-0">
        <v-list-item class="white--text" v-for="link in links" :key="link.text" router :to="link.route">
          {{ link.text }}
        </v-list-item>
      </v-list>
      <v-spacer></v-spacer>
      <v-btn elevation='0' large color="info" class="white--text">Download</v-btn>
    </v-app-bar>

    <v-navigation-drawer app v-model="drawer" class="info darken-1">
      <v-list class="transparent pt-3">
        <v-list-item class="white--text drawer-link" v-for="link in links" :key="link.text" router :to="link.route">
          {{ link.text }}
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
  </header>
</template>

<script>
import $ from 'jquery'

export default {
  name: 'Navbar',

  data() {
    return {
      navCollapse: false,
      drawer: false,
      links: [
        {text: 'Home', route: '/'},
        {text: 'Features', route: '/features'},
        {text: 'Pricing', route: '/pricing'},
        {text: 'Blog', route: '/blog'},
        {text: 'Contact', route: '/contact'},
      ]
    }
  },

  mounted() {
    $(document).scroll(() => {
      if(window.pageYOffset > 0) {
        $('.mybar').removeClass('transparent')
        $('.mybar').addClass('dark')
      }
    })

    let width = window.innerWidth
    if(width < 960) this.navCollapse = true
    else this.navCollapse = false
    window.onresize = () => {
      width = window.innerWidth
      if(width < 960) this.navCollapse = true
      else {
        this.drawer = false
        this.navCollapse = false
      }
    }

  }
}
</script>

<style lang="scss">
@import '../../assets/sass/config';
.mybar {
  transition: .7s;

  &.dark {
    background: #000 !important;
  }
}

.drawer-link {
  transition: .3s;

  &.v-list-item--active, &:hover {
    background: darken($color: $info, $amount: 10)
  }
}
</style>